import React, { useEffect, useState } from "react";
import "./Card.scss";
import data from "../../asset/data";

const Card = (props) => {
    const item = props.datacart;

  return (
    <div>
      <div className="card-home">
        <div className="card_home_img">
          <img
            src={item.anh}
            alt=""
          />
        </div>
        <div className="card_home_content">
          <p>M Phng : {item.maphong}</p>
          <p>Loi Phng : {item.loaiphong}</p>
          <p>Ngi  : {item.songuoithue}/{item.songuoio} (ngi)</p>
          <p>Trng thi : {item.trangthai}</p>
          <p>
            gi : <span>{item.gia}</span> $
          </p>
          <div className="card_home_content_1">
            <p>
              Tnh Trng : 
            </p>
            <div className="tinhtrnag" style={{backgroundColor : `${item.mautinhtrang}`}}></div>
          </div>
          <div className="card_home_content_buton">
            <button>t Phng</button>
            <button>Tr Phng</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
